import RootStore from '../../store/RootStore/root-store';import { LoadingEnum } from '../../store/Type/models';export class AuthStoreService {	rootStore: typeof RootStore;	constructor(rootStore: typeof RootStore) {		this.rootStore = rootStore;	}	async checkAuth(): Promise<void> {		this.rootStore.Notification.setIsLoading(LoadingEnum.fetching);		try {			await this.rootStore.AuthStore.checkAuth();			await this.rootStore.WalletStoreService.getWallets(this.rootStore.AuthStore.user._id);		} catch (e) {			console.log(e);		} finally {			this.rootStore.Notification.setIsLoading(LoadingEnum.success);		}	}}export default AuthStoreService;